.container {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  z-index: 999;
  background: var(--background);
  border: 1px solid rgba(76, 76, 255, 0.2);
  box-shadow: 0px 24px 32px rgba(0, 0, 0, 0.04), 0px 16px 24px rgba(0, 0, 0, 0.04), 0px 4px 8px rgba(0, 0, 0, 0.04), 0px 0px 1px rgba(0, 0, 0, 0.04);
  border-radius: 40px;
}

.modalBox {
  position: relative;
}

.btnClose {
  position: absolute;
  top: calc(var(--offset-base-size) * 15);
  right: calc(var(--offset-base-size) * 10);
  border: none;
  padding: 0;
  background: transparent;
  line-height: 0;
  cursor: pointer;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-height: 64px;
}

.header svg {
  cursor: pointer;
}
